function channelsListItem(classes){
  var channelsList =  data.channels.map(function(channel){
    return  <React.Fragment>
            <ListItem alignItems="flex-start" button  onClick = {event => null}>
              <ListItemText
                primary= {channel.bp}
                secondary={
                  <React.Fragment>
                    <Typography component="span" className={classes.inline} color="textPrimary">
                      {channel.event}
                    </Typography>
                    {channel.productName}
                  </React.Fragment>
                }
                
              />
            </ListItem>
            <Divider/>
            </React.Fragment>
  });

  return channelsList;
}

class PrimarySearchAppBar extends React.Component {
  componentWillMount() {
    document.body.style.margin = 0;
    //document.body.style.height = "700px";
  }
  render() {
    const { classes } = this.props;
    var channelsList = channelsListItem(classes);
    return (
      <div className={classes.root}>
        
        <div className = {classes.header}>
          <AppBar position="static" style={{  boxShadow: 'none'}}>
            <Typography variant="h5" className={classes.title}>
                  All Messages
            </Typography>
            <Toolbar style = {{width: "100%"}}>
                <div className={classes.search}>
                  <div className={classes.searchIcon}>
                    <SearchIcon />
                  </div>
                  <InputBase
                    placeholder="Searchâ€¦"
                    classes={{
                      root: classes.inputRoot,
                      input: classes.inputInput
                    }}
                  />
                </div>
            </Toolbar>
          </AppBar>
        </div>


        <div className = {classes.content} style = {{marginLeft: 10, marginRight: 10}}>
          <List>
            {channelsList}
          </List>
        </div> 
      

      <BottomNavigation
            showLabels className = {classes.footer}>
            <BottomNavigationAction icon={<RefreshIcon />} />
            <BottomNavigationAction label= "Updated" />
            <BottomNavigationAction icon={<SettingsIcon />} />
        </BottomNavigation>
    </div> 
    );
  }
}

PrimarySearchAppBar.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(PrimarySearchAppBar);
